.reg-button{
    display: inline-block;
    text-align: center;
    margin: 10px;
    padding: 10px 20px;
    background-color: lightgreen;
    border: 1px solid grey;
    border-radius: 45px;
    box-shadow: 1px 1px grey;
    text-decoration: none;
    color: black;
    cursor: pointer;
}

.reg-button:hover{
    background-color: #3e8e41;
}

.reg-button:active{
    background-color: #3e8e41;
    box-shadow: 1px 1px #666;
    transform: translateY(2px) translateX(2px);
}