.ResumeButton{
    display: inline-block;
    margin: 20px;
    text-align: center;
    border: 1px solid black;
    background: transparent;
    width: 100%;
    max-width: 300px;
    min-height: 80px;
    line-height: 80px;
    text-decoration: none;
    color: black;
    font-size: 20px;
    cursor: pointer;
}

.ResumeButton:hover{
    text-decoration: underline;
}